'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { motion } from 'framer-motion'
import supabase from '@/lib/supabaseBrowserClient'
import AnimatedProfilePanel from '@/components/animatedProfilePanel'
import EdgeTab from '@/components/edgeTab'

export default function FindPage() {
  const [jobs, setJobs] = useState<any[]>([])
  const [userId, setUserId] = useState<string | null>(null)
  const router = useRouter()

  useEffect(() => {
    const load = async () => {
      const { data: userData } = await supabase.auth.getUser()
      const user = userData.user
      if (user) {
        setUserId(user.id)
        loadOtherJobs(user.email ?? '')
      }
    }
    load()
  }, [])

  const loadOtherJobs = async (email: string) => {
    const { data, error } = await supabase
      .from('jobs')
      .select(`*, claims ( id, claimer_email )`)
      .neq('poster_email', email)
      .order('created_at', { ascending: false })

    if (!error && data) {
      const jobsWithClaimStatus = data.map((job) => ({
        ...job,
        claimedByUser: job.claims?.some(
          (claim: { claimer_email: string }) => claim.claimer_email === email
        ),
      }))
      setJobs(jobsWithClaimStatus)
    }
  }

  const handleClaim = async (jobId: string) => {
    const { data: userData } = await supabase.auth.getUser()
    const email = userData.user?.email
    if (!email) return

    const { error } = await supabase.from('claims').insert({
      job_id: jobId,
      claimer_email: email,
    })

    if (!error) {
      loadOtherJobs(email)
    }
  }

  return (
    <div className="relative min-h-screen">
      <div className="fixed inset-0 z-0 bg-cadet" />

      <motion.div
        initial={{ x: '100%' }}
        animate={{ x: 0 }}
        exit={{ x: '-100%' }}
        transition={{ duration: 0.6 }}
        className="absolute z-10 w-full h-full overflow-auto bg-apricot"
      >
        {userId && <AnimatedProfilePanel userId={userId} />}

        <div className="relative px-6 max-w-3xl mx-auto pt-6">
          <h1 className="text-2xl font-bold mb-6 text-center text-white">
            Available Jobs
          </h1>
          <div className="space-y-6">
            {jobs.map((job) => (
              <div key={job.id} className="border p-4 rounded bg-white">
                <h3 className="text-lg font-semibold">{job.title}</h3>
                <p className="text-gray-600">{job.description}</p>
                <p className="text-sm text-gray-400 mt-1">
                  {job.is_paid ? 'ðŸ’° Paid' : 'ðŸš« Unpaid'}
                </p>
                <p className="text-xs text-gray-500 mt-2">
                  Posted by: {job.poster_email?.split('@')[0] || 'unknown'}
                </p>

                {job.claimedByUser ? (
                  <button
                    disabled
                    className="mt-4 bg-gray-400 text-white px-4 py-2 rounded cursor-not-allowed"
                  >
                    âœ… Claimed
                  </button>
                ) : (
                  <button
                    onClick={() => handleClaim(job.id)}
                    className="mt-4 bg-black text-white px-4 py-2 rounded hover:bg-gray-800 transition"
                  >
                    Claim Job
                  </button>
                )}
              </div>
            ))}
          </div>
        </div>

        {/* Replace old sidebar */}
            <EdgeTab
                direction="left"
                colorClass="bg-cadet"
                label="Post â†’"
                navigateTo="/post"
                textColor="text-black"
            />
        </motion.div>
    </div>
  )
}