'use client'

import { useState, useEffect, useRef } from 'react'
import { motion } from 'framer-motion'
import supabase from '@/lib/supabaseBrowserClient'

interface ProfileData {
  id: string
  name: string | null
  description: string | null
  tags: string[] | null
}

export default function AnimatedProfilePanel({ userId }: { userId: string }) {
  const [expanded, setExpanded] = useState(false)
  const [hovering, setHovering] = useState(false)
  const [profile, setProfile] = useState<ProfileData | null>(null)
  const panelRef = useRef<HTMLDivElement | null>(null)

  useEffect(() => {
    const fetchProfile = async () => {
      const { data } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', userId)
        .single()

      if (data) setProfile(data)
    }

    if (userId) fetchProfile()
  }, [userId])

  useEffect(() => {
    if (!expanded) return

    const handleClickOutside = (event: MouseEvent) => {
      if (
        panelRef.current &&
        !panelRef.current.contains(event.target as Node)
      ) {
        setExpanded(false)
      }
    }

    document.addEventListener('mousedown', handleClickOutside)
    return () => {
      document.removeEventListener('mousedown', handleClickOutside)
    }
  }, [expanded])

  const handleClick = () => {
    setExpanded(!expanded)
  }

  const showContent = hovering || expanded

  return (
    <>
      {expanded && (
        <div className="fixed top-0 left-0 h-screen w-2 bg-orange-400 z-40" />
      )}

      <motion.div
        ref={panelRef}
        layout
        initial={false}
        transition={{ type: 'spring', stiffness: 400, damping: 30 }}
        onMouseEnter={() => setHovering(true)}
        onMouseLeave={() => setHovering(false)}
        onClick={handleClick}
        className={`fixed top-0 right-0 z-50 shadow-lg overflow-hidden cursor-pointer bg-neutral-100 text-black p-4
          ${expanded ? 'h-screen w-[30vw]' : hovering ? 'w-48 h-48' : 'w-16 h-16'}
        `}
      >
        {showContent && profile && (
          <div className="flex flex-col justify-between h-full">
            <h2 className="text-lg font-bold truncate">{profile.name}</h2>

            {expanded && (
              <>
                <p className="mt-2 text-gray-600 whitespace-pre-wrap text-sm">
                  {profile.description}
                </p>
                <div className="flex flex-wrap gap-2 mt-4">
                  {profile.tags?.map((tag) => (
                    <span
                      key={tag}
                      className="px-2 py-1 bg-gray-200 rounded text-xs"
                    >
                      {tag}
                    </span>
                  ))}
                </div>
              </>
            )}
          </div>
        )}
      </motion.div>
    </>
  )
}
